@import '../../sass/variables';

.requisitos {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 40vh;
    background-color: aquamarine;

    &_title {
        margin-top: 2.3rem;
        text-align: center;
        color: white;
        font-size: 1.6rem;
        font-weight: bold;
    }
}

.verde {
    background-color: $verde;
}

.rosa {
    background-color: $rosa;
}