@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600;800&display=swap');

*,
::after,
::before {
    box-sizing: border-box;
    padding: 0%;
    margin: 0%;
}

html {
    font-size: 63%;
    font-family: 'Poppins', sans-serif;
}

body {
    background-color: rgb(238, 238, 238);
}