.buttonB {
    cursor: pointer;
    padding: 1rem 2rem;
    border-radius: 3rem;
    width: 18rem;
    font-size: 1.7rem;
    color: #ccc;
    background-color: white;
    transition: all .2s ease-in-out;
    border: 3.5px solid transparent;
    margin-bottom: 3rem;

    &:hover {
        background-color: whitesmoke;
        color: black;
    }
}

.isActive {
    background-color: black;
    color: white;
    border: 3.5px solid rgb(173, 173, 173);

    &:hover {
        background-color: rgb(66, 66, 66);
        color: white;
    }
}

.container_btns {
    display: flex;
    justify-content: space-around;
}